*{margin:0;padding:0;box-sizing:border-box;list-style:none}body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-height:100vh;font-size:1.3rem;background-color:#0c0c0c;color:rgba(255,255,255,.6)}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Roboto",monospace}.title{text-align:center;margin-bottom:30px}.wrapper{display:flex;flex-direction:column;width:calc(100vw - 20px);max-width:600px;min-width:300px;margin:0 auto;padding:10px 0px}.user{display:flex;height:90px;background-color:rgba(65,65,65,.2);padding:10px;border-radius:5px}.user__content{display:flex;width:100%;margin-right:20px}.user__avatar{width:70px;height:100%;margin-right:15px}.user__avatar img{max-width:100%;height:100%;border-radius:3px}.user__name{color:rgba(255,255,255,.87);font-size:1.5rem;align-self:center}@media(max-width: 768px){.user__name{font-size:1.3rem}}.user__dots{position:relative;cursor:pointer;height:100%;width:20px}.user__dots::after{position:absolute;top:20%;content:"â ‡";font-size:2rem}.user__dropdown{position:absolute;opacity:0;pointer-events:none;transform:translateX(0px)}.user__dropdown__active{position:absolute;opacity:1;transform:translateX(-30px);right:15px;top:-10px;width:110px;height:129%;border-radius:5px;background-color:#bb86fc;transition:opacity .1s ease,transform .5s ease-in-out}.user__dropdown__active li{position:absolute;font-size:1.2rem;vertical-align:middle;text-align:center;top:50%;transform:translateY(-50%)}.user__dropdown__active li a{text-decoration:none;color:#0c0c0c}